import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import { ApiService } from "../shared/api.service";
import { findIndex } from 'lodash';
import { MatTable } from "@angular/material";

interface Participant {
  id: string;
  points: number;
  participant: any;
  position: number;
  winningAnswers?: string[];
}

@Component({
  selector: 'app-user-stats',
  templateUrl: './user-stats.component.html',
  styleUrls: ['./user-stats.component.scss']
})
export class UserStatsComponent implements OnInit, OnDestroy {

  api: any;
  interValRef: any;
  @ViewChild(MatTable) table: MatTable<any>;
  winners: Participant[] = [];
  displayedColumns: string[] = [ 'position', 'participant', 'points', 'winning answers' ];

  constructor(private apiService: ApiService) { }

  ngOnInit() {
    this.api = this.apiService.api;
    this.getFavouriteToots();
    this.interValRef = setInterval(() => {
      this.getFavouriteToots();
    }, 5000);
  }

  ngOnDestroy(): void {
    window.clearInterval(this.interValRef);
  }

  getFavouriteToots(): void {
    this.api.get('/favourites', (data, xhr) => {
      this.winners = [];
      let responseHeaders = xhr.getAllResponseHeaders();
      responseHeaders.indexOf('link');
      data.map(toot => {
        let userIndex = findIndex(this.winners, { id: toot.account.id });
        if (userIndex < 0) {
          this.winners.push({
            points: 1,
            position: 1,
            id: toot.account.id,
            participant: toot.account,
            winningAnswers: toot.tags
          });
        } else {
          this.winners[userIndex].points++;
          this.winners[userIndex].winningAnswers = this.winners[userIndex].winningAnswers.concat(toot.tags);
        }
        this.sortWinners();
        this.table.renderRows();
      })
    });
  }

  sortWinners(): void {
    this.winners = this.winners.sort((first, second) => {
      return second.points - first.points;
    })
    this.winners.map((winner, index) => {
      winner.position = index + 1;
    })
  }
}
